body {
  font-family: Garamond;
}
.header
{
  font-style: italic;
  background: #853535;
}

#menu-table
{
  text-align: left;
  background: #f2f2f2;
  padding-top: 2px;
  padding-bottom: 2px;
  width: 100%;
}

tr
{
  background: rgba(0, 0, 0,0);
}

.menu-layout
{
  text-align: center;
  max-width: 740px;
  margin:auto;
}

.list-item {
  display: list-item;
  list-style-type: decimal;
  list-style-position: inside;
}